<template>
  <div>
      <transition name="slide-fade" mode="out-in">
        <Form v-if="isLogin" ref="loginForm" :model="user"  :rules="rules" key="1">
          <FormItem prop="name">
            <Input v-model="user.name" placeholder="" clearable>
              <span slot="prepend">
                <Icon :size="16" type="ios-person"></Icon>
              </span>
            </Input>
          </FormItem>
          <FormItem prop="password">
            <Input v-model="user.password" type="password" placeholder="" clearable>
              <span slot="prepend">
                  <Icon :size="14" type="md-lock"></Icon>
              </span>
            </Input>
          </FormItem>
            <Button type="primary" @click="login" :loading="loginging" long>
              <span v-if="loginging"></span>
              <span v-else></span>
            </Button>
            <p class="login-tip">123</p>
        </Form>
        <Form v-else ref="registerForm" :model="ruser" :rules="resigsterRules" key="2">
          <FormItem prop="name">
            <Input v-model="ruser.name" placeholder="" clearable>
              <span slot="prepend">
                <Icon :size="16" type="ios-person"></Icon>
              </span>
            </Input>
          </FormItem>
          <FormItem prop="password">
            <Input v-model="ruser.password" placeholder="" clearable>
              <span slot="prepend">
                <Icon :size="14" type="md-lock"></Icon>
              </span></Input>
          </FormItem>
          <FormItem  prop="passwordCheck">
            <Input v-model="ruser.passwordCheck" placeholder="" clearable>
              <span slot="prepend">
                <Icon :size="16" type="md-lock"></Icon>
              </span>
            </Input>
          </FormItem>
          <FormItem prop="email">
            <Input v-model="ruser.email" placeholder="" clearable>
              <span slot="prepend">
                <Icon :size="16" type="ios-mail"></Icon>
              </span>
            </Input>
          </FormItem>
          <Button type="primary" long></Button>
        </Form>
      </transition>
  </div>
</template>

<script>
import './loginForm.less'
import { mapActions } from 'vuex'

export default {
  name: 'LoginForm',
  data() {
      const validatePass = function (rule, value, callback) {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (
            this.ruser.passwordCheck !== '' &&
            this.ruser.passwordCheck.length > 0
          ) {
            this.$refs.registerForm.validateField('passwordCheck');
          }
          if (this.ruser.passwordCheck === '') {
            this.$refs.registerForm.validateField('passwordCheck');
          }
          callback();
        }
      }.bind(this);
      const validatePassCheck = function (rule, value, callback) {
        if (this.ruser.password === '') {
          this.$refs.registerForm.validateField('password');
        } else if (value === '' || value.length <= 0) {
          callback(new Error(''));
        } else if (this.ruser.password !== value) {
          callback(new Error(''));
        } else {
          callback();
        }
      }.bind(this);

      return {
        isLogin: true,
        loginging: false,
        rules: {
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          password: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          passwordCheck: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          email: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        resigsterRules: {
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          password: [{
              validator: validatePass,
              trigger: 'blur'
            },
            {
              required: true,
              message: '',
              trigger: 'blur'
            }
          ],
          passwordCheck: [{
              validator: validatePassCheck,
              trigger: 'blur'
            },
            {
              required: true,
              message: '',
              trigger: 'blur'
            }
          ],
          email: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              type: 'email',
              message: 'email',
              trigger: 'blur'
            }
          ]
        },
        user: {
          name: '',
          password: ''
        },
        ruser: {
          name: '',
          password: '',
          passwordCheck: ''
        }
      };
    },
    methods: {
      ...mapActions([
        'handleLogin'
      ]),
      gotoRegister: function () {
        this.isLogin = !this.isLogin;
      },
      async login() {
        this.loginging = true;
    
        await this.handleLogin(this.user).then(res => {
          this.$Message.info({
            content: ''
          })
          this.$router.push('/home')
        }).catch( err => {
          console.log(this);
          
          this.$Message.error({
            content: err.message
          })
        })

        this.loginging = false
      }
    }
}
</script>
