import Axios from 'axios'
// import vm from '@/main'
const service = Axios.create({
  timeout: 4000
})

export default service;

service.interceptors.response.use(
  response => {
    let { data } = response;

    return { data: data };
  },
  error => {
    //  
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = ``
          break
        case 401:
          error.message = ''
          break
        case 403:
          error.message = ''
          break
        case 404:
          error.message = ','
          break
        case 405:
          error.message = ''
          break
        case 408:
          error.message = ''
          break
        case 500:
          error.message = ''
          break
        case 501:
          error.message = ''
          break
        case 502:
          error.message = ''
          break
        case 503:
          error.message = ''
          break
        case 504:
          error.message = ''
          break
        case 505:
          error.message = 'http'
          break
        default:
          error.message = `${error.response.status}`
      }
    } else {
      error.message = ''
    }
    // vm.$Message.error({
    //     content: ` : ${error.message}`
    // });
    return Promise.reject(error.message);
  }
);