<template>
  <Modal v-model="toShow" @on-cancel="actionCancel">
    <div slot="header">
      <Tag type="border">:<span>{{number}}</span></Tag>
    </div>
    <Row style="display:table;">
      <RadioGroup v-model="printType" style="padding-left:10px;display: table-cell; vertical-align: middle;">
        <span> : </span>
        <Radio label=""></Radio>
        <Radio label=""></Radio>
        <Radio label="16"></Radio>
      </RadioGroup>
    </Row>

    <Row v-if="printType === ''">
      <Divider dashed />
      <input type="file" id="btn_file" style="display:none" @change="getFile"></input>
      <!-- <input  type="file"  @change="getFile" ></input> -->
      <Button type="primary" @click="F_Open_dialog"></Button>
      <Card v-if="imageSource !== ''" :dis-hover="true" style="margin-top:10px">
        <div style="text-align:center">
          <img :src="imageSource" alt="" style="width:98%">
          <h3>{{imageName}}</h3>
        </div>
      </Card>
    </Row>
    <Row v-else>

      <Form :label-width="40">
        <Divider dashed />
        <FormItem label="" class="formitemMargin">
          <Input v-model="sendData.content"></Input>
        </FormItem>
      </Form>
    </Row>
    <Form inline :label-width="90">
      <Row v-if="printType === ''">
        <Divider dashed />
        <Col span=12>
        <FormItem label="">
          <Input v-model="sendData.imgW"></Input>
        </FormItem>

        </Col>
        <Col span=12>
        <FormItem label="">
          <Input v-model="sendData.imgH"></Input>
        </FormItem>
        </Col>
      </Row>
      <Row v-if="printType === ''">
        <Col span=12>
        <FormItem label="X" class="formitemMargin">
          <Input v-model="sendData.orgX"></Input>
        </FormItem>

        </Col>
        <Col span=12>
        <FormItem label="Y" class="formitemMargin">
          <Input v-model="sendData.orgY"></Input>
        </FormItem>
        </Col>
      </Row>

      <Row>
        <Divider dashed />
        <Col span=12>
        <FormItem label="" style="width:95%;" class="formitemMargin">
          <Select v-model="sendData.dataFmt">
            <Option value="img">img</Option>
            <Option value="text">text</Option>
            <Option value="dat">dat</Option>
          </Select>
        </FormItem>
        </Col>
        <Col span=12>
        <FormItem label="" style="width:95%;" class="formitemMargin">
          <Select v-model="sendData.encodeType">
            <Option value="string">string</Option>
            <Option value="base64">base64</Option>
            <Option value="hex">hex</Option>
            <Option value="json">json</Option>
          </Select>
        </FormItem>
        </Col>
      </Row>

      <Row>
        <Divider dashed />
        <Col span=12>
        <FormItem label="" style="width:95%;" class="formitemMargin">
          <Select v-model="sendData.jobType">
            <Option value="print">print</Option>
            <Option value="control">control</Option>
            <Option value="config">config</Option>
            <Option value="DFU">DFU</Option>
          </Select>
        </FormItem>
        </Col>
        <Col span=12>
        <FormItem label="" style="width:95%;" class="formitemMargin">
          <Select v-model="sendData.devType">
            <Option value="wifi">wifi</Option>
            <Option value="device">device</Option>
          </Select>
        </FormItem>
        </Col>
      </Row>
    </Form>

    <Row v-if="msg">
      <Divider dashed />
      <h5>:</h5>
      <p style='display:block;word-break: break-all;word-wrap: break-word;'>{{JSON.stringify(msg)}}</p>
    </Row>

    <div slot="footer">
      <Button type="text" @click="actionCancel"></Button>
      <Button type="primary" @click="actionOK"></Button>
    </div>
    <Spin size="large" fix v-if="isLoading"></Spin>
  </Modal>
</template>

<script>
  import './printView.less'
  import {
    Base64
  } from 'js-base64';
  import {
    submitJob
  } from '@/api/info'

  export default {
    name: 'PrintView',
    props: {
      'json': Object,
      'show': Boolean,
      'number': String
    },
    computed: {
      toShow: {
        set(value) {
          console.log(this.show);
        },
        get: function () {
          return this.show
        }
      },
      data: {
        set(value) {
          console.log(value);
        },
        get() {
          if (this.json) {
            let data = {}
            let items = []
            data.items = items
            return data
          } else {
            return null
          }
        }
      }
    },
    watch: {
      show: function () {
        this.msg = this.imageName = this.imageSource = ''
      }
    },
    data() {
      return {
        op: 0,
        wod: true,
        printType: '',
        content: '10170000',
        sendData: {
          dataFmt: 'dat',
          devType: 'wifi',
          encodeType: 'hex',
          jobType: 'control',
          content: '10170000'
        },
        imageSource: '',
        imageName: '',
        isLoading: false,
        msg: ''
      }
    },
    methods: {
      async actionOK() {
        console.log(this.printType);
        this.isLoading = true
        this.msg = ''
        if (this.printType === '') {
          this.sendData.datas = this.imageSource
        } else {
          //  hex
          if (this.sendData.encodeType === 'base64') {
            console.log(this.sendData.content);

            this.sendData.datas = Base64.encode(this.sendData.content)
            console.log(this.sendData.datas);
          } else {
            this.sendData.datas = this.sendData.content
          }
        }
        this.sendData.number = this.number
        console.log(JSON.stringify(this.sendData));

        this.$emit('action', 'ok')
        this.showPrint = true

        await submitJob(this.sendData).then(res => {
          this.msg = res.data
        }).catch(err => {
          console.log(err);
          this.$Message.error({
            content: ` : ${err}`
          });
        })

        this.isLoading = false
      },
      actionCancel() {
        this.$emit('action', 'cancel')
      },
      changeClick(type) {
        this.$emit('get-config', type)
        this.wod = type === 'WIFI'
      },
      getFile(e) {
        let file = e.target.files[0]

        if (!file) {
          this.imageName = this.imageSource = ''
          console.log('');

          return
        }

        let res = /image\/\w+/.test(file.type)
        if (!res) {
          this.$Message.error({
            content: ''
          })
          this.imageName = this.imageSource = ''
          return
        }

        console.log(`:${file.name} :${file.size} :${file.type} path:${e.target.value}`);
        this.imageName = file.name
        let reader = new FileReader();
        reader.readAsDataURL(file)
        reader.onload = function (event) {
          console.log(event.target.result);
          this.imageSource = event.target.result
        }.bind(this)
      },
      F_Open_dialog() {
        document.getElementById('btn_file').click();
      }
    }
  }

</script>
