<template>
<div>
  <div class="nameDiv"> <span class="name">{{userName}}</span>
  </div>
  <div class="user-dropList">
    <Dropdown @on-click='handleClick'>
      <Avatar icon="ios-person" />
      <Icon :size="18" type="md-arrow-dropdown"></Icon>
      <DropdownMenu slot="list">
        <DropdownItem name="logout"></DropdownItem>
      </DropdownMenu>
    </Dropdown>
  </div>
</div>
</template>

<script>
  import './user.less'
  import { mapActions } from 'vuex'
  export default {
    name: 'User',
    computed: {
      userName() {
        return this.$store.state.user.userName
      }
    },
    methods: {
      ...mapActions([
        'handleLogout'
      ]),
      logout() {
        this.handleLogout().then(() => {
            this.$router.push({
              name: 'login'
            })
          })
      },
      handleClick (name) {
        if (name === 'logout') {
          this.logout()
        }
      }
    }
  }

</script>
